
@font-face {
    font-family: 'Martian Mono';
    src: url('/fonts/MartianMono-Regular.woff2') format('woff2');
    font-weight: 400;
}
/*
@font-face {
    font-family: 'Martian Mono';
    src: url('/fonts/MartianMono-Bold.woff2') format('woff2');
    font-style: bold;
}
*/
body {
    font-family: 'Martian Mono';
    color: #000;
    font-size: 16px;
    text-align: left;
    word-wrap: break-word;
    overflow-wrap: break-word;
    line-height: 1.5;
    margin: auto;
    padding: 20px;
    background-color: #fff;

}
a {
    color: #000;//#3273dc;
}
a:visited {
    color: #888888//#8b6fcb;
}

.navigation-header{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "logo navigation";
    align-items: center;
    justify-content: stretch;
    text-align: center;
    margin-bottom: 10px;
}

.navigation-header > .logo {
    grid-area: logo;
}

.navigation-header a {
    text-decoration: none;
    color:#000;
}

.navigation-header > .nav-items {
    grid-area: navigation;

}

@media (max-width: 750px){
    .navigation-header {
    grid-template-columns: auto;
    grid-template-areas: "logo"
                        "navigation";
    }

}
.logo{
    font-size: 14px;
    text-decoration: none;
    color:#000;
    border-top: none;
}
nav > a {
    font-size: 20px;
    margin-right: 10px;
    color: #000;
    border-top: 3px solid #000;
}
img {
    padding: 3px;
    border: 3px solid #000;
    max-width: 100%;
    margin: 0 auto;
}

h1,h2,h3,h4,h5,h6{
    color:#000;
}

/*code*/
code {
    padding: 2px 5px;
}

pre,code {
    font-family: inherit;
    background-color: #f2f2f2;
}
pre code {
    color:#222;
    display: block;
    padding: 20px;
    white-space: pre;
    overflow: auto;
    border: 3px solid #000;
    background-color: #fff;
}
blockquote{
    border-left: 1px solid #999;
    color: #222;
    padding-left: 20px;
    font-style: inherit;
}

/*Syntax Highlighting Generated by Rouge*/
