/*index page*/
.index-page{
    display:grid;
    gap:40px;
    grid-template-columns: auto 12rem minmax(0,38rem) auto;
    grid-template-areas: ". portrait intro .";
}
.index-page > img {
    max-width: 12rem;
    grid-area: portrait;
}
.index-page > div{
    grid-area: intro;
}
@media(max-width: 750px) {
    .index-page {
        gap:0px;
        grid-template-columns: auto;
        grid-template-areas: "portrait"
                             "intro";
    }
    .index-page h2 {
        text-align: center;
    }
}
/*article list*/
.list-page,
.article-page {
   display: grid;
   grid-template-columns: auto minmax(0, 50rem) auto;
   grid-template-areas: ". content .";
}
content { 
    grid-area: content;
    line-height: 1.6;
}
ul.blog-posts {
    list-style-type: none;
    padding: unset;
}
ul.blog-posts li {
    display: flex;
}
ul.blog-posts li span {
    flex: 0 0 130px
}
